'use strict';

var globalConfig = {};

globalConfig.Browser = {
  versions: function () {
    var u = navigator.userAgent, app = navigator.appVersion;
    return {//移动终端浏览器版本信息
      webKit: u.indexOf('AppleWebKit') > -1, //苹果、谷歌内核
      mobile: !!u.match(/AppleWebKit.*Mobile.*/) || !!u.match(/AppleWebKit/), //是否为移动终端
      ios: !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/), //ios终端
      android: u.indexOf('Android') > -1 || u.indexOf('Linux') > -1, //android终端或者uc浏览器
      iPhone: u.indexOf('iPhone') > -1 || u.indexOf('Mac') > -1, //是否为iPhone或者QQHD浏览器
      iPad: u.indexOf('iPad') > -1, //是否iPad
      webApp: u.indexOf('Safari') == -1, //是否web应该程序，没有头部与底部
      weixin: u.toLowerCase().indexOf('micromessenger') > -1, //是否微信内置浏览器,
      showjoyiOS: u.indexOf('ShowJoyiOS') > -1, // //是否iOS官方客户端内置浏览器
      showjoyAndroid: u.indexOf('ShowJoyAndroid') > -1, //是否Android官方客户端内置浏览器
      iOSAPP: u.indexOf('iOSAPP') > -1, // //是否iOS官方客户端内置浏览器
      androidAPP: u.indexOf('androidAPP') > -1, //是否Android官方客户端内置浏览器
      showjoyShopiOs: u.indexOf('showjoyshopiOS') > -1, 
      showjoyShopAndroid: u.indexOf('showjoyShopAndroid') > -1
    };
  } (),
  language: (navigator.browserLanguage || navigator.language).toLowerCase()
};

globalConfig.Env = (function () {
  var hostname = window.location.hostname;
  if ((hostname.indexOf('showjoy.net') !== -1) || (hostname.indexOf('ggl.net') !== -1)) {
    return 'test';
  } else if ((hostname.indexOf('showjoy.com') !== -1) || (hostname.indexOf('ggl.com') !== -1)) {
    return 'alive';
  }
})();

module.exports = globalConfig;


